<template>
  <div class="ClassBasisTotalBackground">
    <table>
      <thead>
        <tr>
          <th style="width: 2%">课程模块</th>
          <th style="width: 1%">修读方式</th>
          <th style="width: 1.5%">课程名</th>
          <th style="width: 1.5%">课程编号</th>
          <th style="width: 0.5%">学分</th>
          <th style="width: 0.5%">学时</th>
          <th style="width: 3%">开课学期</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="data in ClassBasisInformationTable" :key="data.courseNumber">
          <td>{{ data.courseModule }}</td>
          <td>{{ data.studyMethod }}</td>
          <td>{{ data.courseName }}</td>
          <td>{{ data.courseNumber }}</td>
          <td>{{ data.credit }}</td>
          <td>{{ data.studyTime }}</td>
          <td>{{ data.courseStartDateTime }}</td>
        </tr>
      </tbody>
    </table>

    <!-- 分页控制 -->
    <!--  <div>
      <button
        @click="changePage(currentPage - 1)"
        :disabled="currentPage === 1"
      >
        Previous
      </button>
      <span>Page {{ currentPage }} of {{ totalPages }}</span>
      <button
        @click="changePage(currentPage + 1)"
        :disabled="currentPage === totalPages"
      >
        Next
      </button>
    </div> -->
  </div>
</template>

<script>
import axios from "@/axios";
export default {
  data() {
    return {
      ClassBasisInformationTable: [],
    };
  },
  mounted() {
    this.fetchData();
  },
  methods: {
    async fetchData() {
      try {
        const informationResponse = await axios.get("/admin/getCourses");
        if (informationResponse.code == 1) {
          this.ClassBasisInformationTable = informationResponse.data.records;
        } else {
          this.$message("数据加载失败!");
        }
      } catch (error) {
        this.$message("数据加载失败!");
        console.error("报错原因：", error);
      }
    },
  },
};
</script>

<style>
.ClassBasisTotalBackground {
  position: relative;
  width: 100%;
  height: 100%;
}

@import "/src/components/ManageSystem/TableStyle.css";
</style>
